@import '../scss/Variables';


.header__cointainer {
    // z-index: -2;
    position: relative;
    min-height: 100vh;
    display: flex;
    justify-content: center;



    &--title {
        position: absolute;
        top: 20%;
        width: 100%;
        text-align: left;
        padding-left: 10%;
        animation: show 2s ease 1;
        transform: translate(0px, 0px);

        @media screen and (min-width: 600px) {
            font-size: 3rem;
            padding-left: 15%;
        }

        @keyframes show {
            from {
                opacity: 0;
                transform: translate(0px, -20px);
            }

            to {
                opacity: 1;
                transform: translate(0px, 0px);
            }
        }

        span.main-title {

            display: inline-block;

            font-family: $header-font;

            &.bounce {
                color: crimson;
                animation: bounce 0.7s linear 1;
            }
        }

        &::before,
        &::after {
            position: absolute;
            left: 5%;
            font-family: $tag-font;
            font-weight: 200;
            opacity: 0.5;
            color: $header-color;

        }

        &::before {
            content: "<h1>";
            top: -25px;
        }

        &::after {
            content: "</h1>";
            bottom: -30px;
        }

    }


}




@keyframes bounce {

    20%,
    50%,
    80%,
    to {
        transform: scale(1, 1);
    }

    40%,
    45% {
        transform: scale(1.75, 0.65);
    }

    60% {
        transform: scale(1.25, 0.75);
    }

    90% {
        transform: scale(1.15, 0.85);
    }

}


@media screen and (min-width: 1140px) {
    .header__cointainer {





        &--title {

            top: 35%;
            width: 100%;
            text-align: left;
            padding-left: 5%;
            animation: show 2s ease 1;
            transform: translate(0px, 0px);



            span.main-title {

                display: inline-block;
                font-family: $header-font;
                font-size: 4.5rem;




                &.bounce {
                    color: crimson;
                    animation: bounce 0.7s linear 1;
                }
            }

        }
    }

}